import React from 'react';
import DocsComponent from './DocsComponent';

function hideDocsDialog() {
    document.getElementById("docs_dialog").style.display = "none";
}

class DocsDialog extends React.Component {
  render() {
    return (
      <div>
        <div className="dialog_shadow"/>
        <div className="dialog_container">
          <div className="dialog"> 
            <div className="flex_container"> 
                <div className="title_text">
                    Документация пользователя Simple Cypher
                </div>
                <a onClick={hideDocsDialog} className="title_text" style={{
                  width: "3vh"
                }}>
                  &#10006;
                </a>
            </div>
            <div className="dialog_content">
              <DocsComponent/>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default DocsDialog;