import React from 'react';
import LicenseComponent from './LicenseComponent';

function hideLicenseDialog() {
    document.getElementById("license_dialog").style.display = "none";
}

class LicenseDialog extends React.Component {
  render() {
    return (
      <div>
        <div className="dialog_shadow"></div>
        <div className="dialog_container">
          <div className="dialog"> 
            <div className="flex_container"> 
              <div className="title_text">
                Лицензионное соглашение Simple Cypher
              </div>
              <a onClick={hideLicenseDialog} className="title_text" style={{
                width: "3vh"
              }}>
                &#10006;
              </a>
            </div>
            <div className="dialog_content">
              <LicenseComponent/>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default LicenseDialog;